.navbar {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  height: 15vh;
  max-width: 100%;
  min-width: 100%;
  z-index: 5;
  background-color: transparent;
}
.navbar__black {
  background-color: black;
  transition-timing-function: ease-in;
  transition: all 0.8s;
}
.header__routers {
  min-width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
}
.header__elements {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1;
}
.header__icon {
  font-size: 3rem !important;
  color: rgb(140, 24, 58);
  text-decoration: none;
}
.header__elements:hover {
  border-bottom: 1px solid rgb(140, 24, 58);
}

@media (max-width: 1048px) {
  .header__icon {
    font-size: 1.6rem !important;
  }
}

.app::-webkit-scrollbar {
