  background: rgb(140, 24, 58);
  max-width: 100%;
  min-width: 100%;
}

.app__container {
  /* padding-top: 19vh; */
  width: 100%;
  /* background: rgb(140, 24, 58); */
}

.app::-webkit-scrollbar {
