}

.project__container {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 220px;
  border: 1px solid black;
  border-radius: 10px;
  margin: 8px 0;
  padding: 5px;
  transition: 0.5s;
}
.project__details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5px;
}
.project__details > button {
  width: 100%;
  height: 50px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: black;
  outline: none;
  border: none;
  font-size: 2rem;
  color: rgb(140, 24, 58);

  transition: 0.5s;
}
.project__details > button:hover {
  cursor: pointer;
  color: #1ae285;
  box-shadow: 0 0 5px #1ae285;
  text-shadow: 0 0 5px #1ae285;
}
.project__img__section {
  flex: 1 1;
  background-color: black;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.project__container__img {
  height: 100px;
  object-fit: contain;
  border-radius: 10px;
}

.project__container:hover {
  color: #1ae285;
  box-shadow: 0 0 5px #1ae285;
  text-shadow: 0 0 5px #1ae285;
}
.techused__icons {
  height: 50px;
}
.techused__container {
  display: flex;
  justify-content: space-around;
}
.modal > p > a {
  color: rgb(140, 24, 58);
}

@media (max-width: 1048px) {
  .project__container__img {
    height: 80px;
    object-fit: contain;
